import React, { useState } from 'react';
import { Box, Typography, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Button } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import RegisterAlert from './RegisterAlert';
import ReplyAlert from './ReplyAlert';

//   
const inquiries = [
  { id: 1, category: '[] ', title: 'Makeup', status: '', inquiryDate: '2024-06-26', replyDate: '' },
  { id: 2, category: '', title: 'Asus Laptop', status: '', inquiryDate: '2024-06-20', replyDate: '2024-06-25' },
  { id: 3, category: '', title: 'Iphone X', status: '', inquiryDate: '2024-06-15', replyDate: '2024-06-25' },
];

const containerStyle = {
  width: '100%',
  padding: 3,
};

const titleStyle = {
  mb: 4,
  color: 'white',
};

const paperStyle = {
  padding: 3,
  borderRadius: 2,
  backgroundColor: '#273142',
  color: 'white',
};

const headerStyle = {
  display: 'flex',
  justifyContent: 'space-between',
  alignItems: 'center',
  mb: 2,
};

const tableHeaderStyle = {
  color: 'white',
};

const tableCellStyle = {
  color: 'white',
};

const InquiryBoard = () => {
  const [openRegister, setOpenRegister] = useState(false);
  const [openReply, setOpenReply] = useState(false);
  const [selectedInquiry, setSelectedInquiry] = useState(null);

  const handleClickOpenRegister = () => {
    setOpenRegister(true);
  };

  const handleCloseRegister = () => {
    setOpenRegister(false);
  };

  const handleClickOpenReply = (inquiry) => {
    setSelectedInquiry(inquiry);
    setOpenReply(true);
  };

  const handleCloseReply = () => {
    setOpenReply(false);
    setSelectedInquiry(null);
  };

  return (
    <Box sx={containerStyle}>
      <Typography variant="h4" sx={titleStyle}>
        
      </Typography>
      <Paper sx={paperStyle}>
        <Box sx={headerStyle}>
          <Typography variant="h6"> </Typography>
          <Button variant="contained" color="primary" startIcon={<AddIcon />} onClick={handleClickOpenRegister}>
             
          </Button>
          <RegisterAlert open={openRegister} handleClose={handleCloseRegister} />
        </Box>
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell sx={tableHeaderStyle}></TableCell>
                <TableCell sx={tableHeaderStyle}></TableCell>
                <TableCell sx={tableHeaderStyle}></TableCell>
                <TableCell sx={tableHeaderStyle}> </TableCell>
                <TableCell sx={tableHeaderStyle}> </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {inquiries.map((inquiry) => (
                <TableRow key={inquiry.id} onClick={() => handleClickOpenReply(inquiry)} sx={{ cursor: 'pointer' }}>
                  <TableCell sx={tableCellStyle}>{inquiry.id}</TableCell>
                  <TableCell sx={tableCellStyle}>{inquiry.title}</TableCell>
                  <TableCell sx={tableCellStyle}>{inquiry.status}</TableCell>
                  <TableCell sx={tableCellStyle}>{inquiry.inquiryDate}</TableCell>
                  <TableCell sx={tableCellStyle}>{inquiry.replyDate}</TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
      {selectedInquiry && <ReplyAlert open={openReply} handleClose={handleCloseReply} inquiry={selectedInquiry} />}
    </Box>
  );
};

export default InquiryBoard;
