// export default Background

const Background = (props) => {
  return (
    <Box
      sx={{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        flexDirection: 'column',
        flex: 1,
        overflow: 'auto',
        mb:0,
      }}
    >
      <Container
        sx={{
          backgroundColor: 'transparent',
          border: '1px solid black',
          mb: 0,
        }}
      >
        {props.name}
      </Container>
      <Container
        sx={{
            height: '75vh',
            mb: 0,
        }}
            //   sx={{
            //     width: 'calc(100% - 40px)', //   20px   
            //       height: '80vh', //     80% 
            //       dispay: 'flex',
            //       alignItems: 'center',
            //       justifyContent: 'center',
            //       padding: 2,
            //       marginTop: '20px', //  
            //       marginBottom: '10px', //  
            //       marginLeft: '10px', //  
            //       marginRight: '10px', //  
            // alignItems: 'stretch',
                
            //   }}  
      >
        {props.contents}
      </Container>
    </Box>
  )
}
  
  export default Background