import React, { useState } from 'react';
import { useForm } from "react-hook-form";
import { useNavigate, Link } from "react-router-dom";
import axios from "axios";
import { Grid, Box, MenuItem, FormControl, Autocomplete,
    InputLabel, Select, TextField, Button, styled } from '@mui/material';
import Stack from '@mui/material/Stack';
import { DemoContainer } from '@mui/x-date-pickers/internals/demo';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import { LocalizationProvider } from '@mui/x-date-pickers/LocalizationProvider';
import { DatePicker } from '@mui/x-date-pickers/DatePicker';
import LongButton from "../Styles/LongButton.js";
import NextButton from "./NextButton.js";
import CustomTextField from '../Styles/CustomTextField.js';
import Background from "../Background";
import EmailAlert from './EmailAlert';

const SignUpForm = ({ marginBottom }) => {

    const { register, handleSubmit, formState: { errors }, setValue, watch, control } = useForm();
    const [alertOpen, setOpen] = React.useState(false);
    const userType = watch('userType'); //    
    const [redirectPath, setRedirectPath] = useState(null); //   
    const formSx = { //  
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        margin: '50px',
        flexDirection: 'column', //   
        maxHeight: 'calc(100vh - 100px)', //         
        overflowY: 'auto', //   
    };
    const handleClickOpen = () => { //   
        setOpen(true);
    };
    const handleClose = () => { //   
        setOpen(false);
    };
    const [role, setRole] = useState(''); //     
    
    //    const
    const navigate = useNavigate(); //    navigate 
    const onNextClick = () => {    //   
        navigate('/login');
    };
    //   const
    const [category, setCategory] = useState('');
    
    //   const
    const [org, setOrg] = useState('');
    const [customOrg, setCustomOrg] = useState('');  //      
    const [isCustomInput, setIsCustomInput] = useState(false);  //    
    const handleOrgChange = (event) => {
        const value = event.target.value;
        setOrg(value);
        setIsCustomInput(value === 'custom');  // ' '    
        if (value !== 'custom') {
            setCustomOrg('');  // ' '    
        }
    };
    //    const
    const [startdate, setStartdate] = React.useState(null);
    //    const
    const [openRegister, setOpenRegister] = useState(false);
    const handleClickOpenRegister = () => {
        setOpenRegister(true);
    };
    const handleCloseRegister = () => {
        setOpenRegister(false);
    };

    return (
        <Background
            name=""
            contents={
            <Box sx={{ display: 'flex', flexDirection: 'column', margin: '50px' }}>
                <div>
                    <CustomTextField
                        label=""
                        id="name"
                        {...register("name", { required: " ." })}
                        inputProps={{ maxLength: 30 }}
                        error={!!errors.name}
                        helperText={errors.name?.message}
                        style={{ marginBottom: errors.name ? '0px' : '23px' }}
                    />
                </div>
                <div>
                    <CustomTextField
                        label=""
                        id="id"
                        {...register("id", {
                            required: " .",
                            pattern: {
                                value: /^[A-Za-z0-9]+$/,
                                message: "   .",
                            },
                            minLength: {
                                value: 5,
                                message: " 5  .",
                            },
                            maxLength: {
                                value: 30,
                                message: " 30  .",
                            },
                        })}
                        // inputProps={{ maxLength: 30 }}
                        error={!!errors.id}
                        helperText={errors.id?.message}
                        style={{ marginBottom: errors.id ? '0px' : '23px' }}
                    />
                </div>
                <div>
                   <CustomTextField
                        label=""
                        id="email"
                        {...register("email", { required: " ." })}
                        inputProps={{ maxLength: 30 }}
                        error={!!errors.email}
                        helperText={errors.email?.message}
                        style={{ marginBottom: errors.email ? '0px' : '23px' }}
                    /> 
                </div>
                <Button variant="contained" color="primary" onClick={handleClickOpenRegister}>
                     
                </Button>
                <EmailAlert open={openRegister} handleClose={handleCloseRegister} />
                <div>
                   <CustomTextField
                        label=""
                        id="pw"
                        {...register("pw", { required: " ." })}
                        inputProps={{ maxLength: 30 }}
                        error={!!errors.pw}
                        helperText={errors.pw?.message}
                        style={{ marginBottom: errors.pw ? '0px' : '23px' }}
                    /> 
                </div>
                <div>
                    <CustomTextField
                        label=" "
                        id="pw"
                        {...register("pw", { required: "  ." })}
                        inputProps={{ maxLength: 30 }}
                        error={!!errors.pw}
                        helperText={errors.pw?.message}
                        style={{ marginBottom: errors.pw ? '0px' : '23px' }}
                    />
                </div>
                <div>
                    <CustomTextField
                        label=""
                        id="phone"
                        {...register("phone", { required: " ." })}
                        inputProps={{ maxLength: 30 }}
                        error={!!errors.phone}
                        helperText={errors.phone?.message}
                        style={{ marginBottom: errors.phone ? '0px' : '23px' }}
                    />
                </div>
                <div>
                    <CustomTextField
                        margin="normal"
                        fullWidth
                        select // drop down    select  
                        label=""
                        value={category}
                        onChange={(e) => setCategory(e.target.value)}
                    >
                        <MenuItem value="director"></MenuItem>
                        <MenuItem value="host"> </MenuItem>
                    </CustomTextField>
                </div>
                <div>
                    <LocalizationProvider dateAdapter={AdapterDayjs}>
                        {/* <DemoContainer components={['DatePicker']}> */}
                            <DatePicker label=" "
                            renderInput={(params) => <CustomTextField {...params} />} 
                            />
                        {/* </DemoContainer> */}
                    </LocalizationProvider>
                </div>
                <div>
                    <LocalizationProvider dateAdapter={AdapterDayjs}>
                        {/* <DemoContainer components={['DatePicker']}> */}
                            <DatePicker label=" "
                            renderInput={(params) => <CustomTextField {...params} />} 
                            />
                        {/* </DemoContainer> */}
                    </LocalizationProvider>
                </div>
                <div>
                    <CustomTextField
                        margin="normal"
                        fullWidth
                        select
                        label=""
                        value={org}
                        onChange={handleOrgChange}
                    >
                        <MenuItem value="custom"> </MenuItem>
                        <MenuItem value="3"></MenuItem>
                        <MenuItem value="4"></MenuItem>
                        <MenuItem value="5"></MenuItem>
                        <MenuItem value="6"></MenuItem>
                        <MenuItem value="7"></MenuItem>
                        <MenuItem value="8"></MenuItem>
                        <MenuItem value="9"></MenuItem>
                        <MenuItem value="10"></MenuItem>
                        <MenuItem value="11"></MenuItem>
                        <MenuItem value="12"></MenuItem>
                        <MenuItem value="13"></MenuItem>
                        <MenuItem value="14">  </MenuItem>
                    </CustomTextField>
                    {isCustomInput && (
                        <CustomTextField
                            fullWidth
                            label=" "
                            value={customOrg}
                            onChange={(e) => setCustomOrg(e.target.value)}
                            margin="normal"
                        />
                    )}
                </div>
                <div>
                    <CustomTextField
                        label=" "
                        id="org_phone"
                        {...register("org_phone", { required: "  ." })}
                        inputProps={{ maxLength: 30 }}
                        error={!!errors.org_phone}
                        helperText={errors.org_phone?.message}
                        style={{ marginBottom: errors.org_phone ? '0px' : '23px' }}
                    />
                </div>
                <div>
                    <NextButton type="submit" onClick={onNextClick}></NextButton>
                </div>
            </Box>
            }
        />
    );
};

export default SignUpForm;