import React from "react";
import { useForm } from "react-hook-form";
import { useNavigate } from "react-router-dom";
import axios from "axios";
import LongButton from "../Styles/LongButton.js";
import SignupButton from "./SignupButton.js";
import CustomTextField from '../Styles/CustomTextField.js';

import Stack from '@mui/material/Stack';

const ResetPasswordForm = ({ marginBottom }) => {
    const navigate = useNavigate();
    const { register, handleSubmit, watch, formState: { errors } } = useForm({mode: "onChange"});
    const [alertOpen, setOpen] = React.useState(false);
    const pwd = watch("pw");
    const pwdCheck = watch("pwCheck");

    const formSx = {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        margin: '50px',
    };

    const onLogIn = async (data) => {
        const { pw } = data;
        navigate('/dashboard');
    };

    return (
        <Stack
            component="form"
            spacing={3}
            noValidate
            autoComplete="off"
            sx={formSx}
            onSubmit={handleSubmit(onLogIn)}
        >
            <div>
                <CustomTextField
                    label=" "
                    id="currentPw"
                    type="password"
                    {...register("currentPw", { required: "  ." })}
                    inputProps={{ maxLength: 30 }}
                    error={!!errors.currentPw}
                    helperText={errors.currentPw?.message}
                    style={{ marginBottom: errors.currentPw ? '0px' : '23px' }}
                />
            </div>
            <div>
                <CustomTextField
                    label=""
                    id="pw"
                    type="password"
                    {...register("pw", {
                        required: "   ." ,
                        minLength: {
                          value: 8,
                          message: '8  .',
                        },
                        pattern: {
                          value: /^(?=.*[A-Za-z])(?=.*\d)(?=.*[!@#$%^&*])[A-Za-z\d!@#$%^&*]{8,}$/,
                          message: ' , ,  .',
                        },
                    })}
                    inputProps={{ maxLength: 30 }}
                    error={!!errors.pw}
                    helperText={errors.pw?.message}
                    style={{ marginBottom: errors.pw ? '0px' : '23px' }}
                />
            </div>
            <div>
                <CustomTextField
                    label=" "
                    id="pwCheck"
                    type="password"
                    {...register("pwCheck", {
                        required: "    .",
                        validate: value => value === pwd || "  ."
                    })}
                    inputProps={{ maxLength: 30 }}
                    error={!!errors.pwCheck}
                    helperText={errors.pwCheck?.message}
                    style={{ marginBottom: errors.pwCheck ? '0px' : '23px' }}
                />
            </div>
            <div>
                <LongButton type="submit" variant="contained"></LongButton>
            </div>
        </Stack>
    );
};

export default ResetPasswordForm;